import { Component, OnInit } from '@angular/core';
import { PostProvider } from '../../providers/post-provider';
import { Router, ActivatedRoute } from '@angular/router';
import { Storage } from '@ionic/storage';

@Component({
  selector: 'app-showcustomer',
  templateUrl: './showcustomer.page.html',
  styleUrls: ['./showcustomer.page.scss'],
})
export class ShowcustomerPage implements OnInit {

  name_customer: string;
  desc_customer: string;
  contact_customer: string;
  customer = false;


  customers: any = [];
  upsaldos: any = [];
  limit: number = 10;
  start: number = 0;
  phone_number: string;
  username: string;
  user_id: any;
  identificacion; any;
  name_destinatario: any;
  ident_destinatario: any;
  saldo: string;
  login_id: any;
  anggota: any;

  id: number;

    constructor(  
    private router: Router,
    private postPvdr: PostProvider,
    private storage: Storage,
    private actRoute: ActivatedRoute
  ) {
    setTimeout(() => {
      this.customer = true;
    }, 3000);
   }
   doRefresh(event) {
    setTimeout(() => {
      this.ionViewWillEnter();
      event.target.complete();
    }, 1500);
  }



  ionViewWillEnter() {
    this.customers = [];
    this.upsaldos = [];    
    this.start = 0;
    this.loadCustomer(this.login_id);    
    this.storage.get('session_storage').then((res) => {
      this.anggota = res;
      this.login_id = this.anggota.login_id;
      this.user_id = this.anggota.user_id;
      this.identificacion = this.anggota.identificacion;
      //this.saldo = this.anggota.saldo;
      this.phone_number = this.anggota.phone_number;
      this.username = this.anggota.username;
    });
  }




   ngOnInit() {
    this.actRoute.params.subscribe((data: any) =>{
    this.id = data.user_id;
    this.name_customer = data.name;
    this.desc_customer = data.desc;
    this.contact_customer = data.contact;
    this.name_destinatario = data.namedest;
    this.ident_destinatario = data.ident;
        console.log(data);

    });
  }

  loadData(event: any) {
    this.start += this.limit;
    setTimeout(() => {
    this.loadCustomer(this.login_id).then(() => {
    event.target.complete();
    });
    },1500);
  }



  loadCustomer(login_id) {
  
    return new Promise(resolve => {
      let body = {
        aksi: 'gettransfer' ,        
        limit : this.limit,
        start : this.start,
        user_id : this.user_id, 
        login_id : this.login_id,
        contact_customer : this.contact_customer,
        name_destinatario : this.name_destinatario,
        ident_destinatario : this.ident_destinatario
       // saldo : this.saldo
       
      };
      //console.log(this.login_id);

      this.postPvdr.postData(body, 'file_aksi.php').subscribe(data => {
        if(data.result) {
          for(let customer of data.result) {
           this.customers.push(customer)
          
           
        }
        }
      
        resolve(true);
      });
     
    });
  }

}
